// lib/mappers/routine.ts

export function mapDbRoutine(db: any) {
  return {
    id: db.id,
    name: db.name,
    createdAt: db.created_at ?? null,
    userId: db.user_id ?? null,

    exercises: (db.exercises ?? []).map((ex: any) => ({
      id: ex.id,
      exerciseId: ex.exercise_id,
      suggestedSets: ex.suggested_sets ?? 0,
      suggestedReps: ex.suggested_reps ?? 10,
    }))
  }
}
